<template>
	<div class="padding-left-right ">
				<el-form-item label=":">
				    <el-input v-model="form.name" placeholder="+++"></el-input>
				  </el-form-item>
				  <el-form-item label=":" >
				    <el-select v-model="form.fabricType" placeholder="" style="width:100%">
				      <el-option label="" value="zhengzhi"></el-option>
				      <el-option label="" value="suozhi"></el-option>
				      <el-option label="" value="maoshan"></el-option>
				      <el-option label="" value="liuzai"></el-option>
				    </el-select>
				  </el-form-item>
				  <el-form-item label=":" >
				    <el-select v-model="form.kindType" placeholder="" style="width:100%">
				      <el-option label="" value="zhengzhi"></el-option>
				      <el-option label="" value="suozhi"></el-option>
				      <el-option label="" value="maoshan"></el-option>
				      <el-option label="" value="liuzai"></el-option>
				    </el-select>
				  </el-form-item>
				  <el-form-item label=":">
				    <el-radio-group v-model="form.status" style="padding-top:8px">
				      <el-radio label=""></el-radio>
				      <el-radio label=""></el-radio>
				      <el-radio label=""></el-radio>
				    </el-radio-group>
		 		</el-form-item>
			</div>
</template>
<script>
	export default{
		name: "sheet",
		data(){
			return {
				form: {
					name: '',
					fabricType: "",
					kindType: '',
					fabricType:'',
					kindType:'',
					status:''
				}
			}
		},
		watch:{
			form:{
				handler(curVal, oldVal){					
					this.$emit('setWorkSheet',curVal)
		},
				deep: true
			}
		},
	}
</script>
<style scoped>
	
</style>


