<template>
	<div>
		<el-form-item label=":">
		    <el-input v-model="dateForm.price" placeholder=""></el-input>
		 </el-form-item>
		 <el-form-item label=":">
		    <el-input v-model="dateForm.totalPrice" placeholder=""></el-input>
		 </el-form-item>
		 <el-form-item label=":">
		    <el-date-picker	  v-model="dateForm.end"   type="date"	  placeholder="">  </el-date-picker>
		 </el-form-item>
		 <el-form-item label=":">
		    <el-date-picker	v-model="dateForm.ready"   type="date"	  placeholder="">  </el-date-picker>
		 </el-form-item>
		<el-form-item label=":">
		    <el-date-picker	  v-model="dateForm.over"   type="date"	  placeholder="">  </el-date-picker>
		 </el-form-item>
	</div>
</template>
<script>
	export default{
		name: "date",
		data(){
			return{
				dateForm: {
		          price: '',
		          totalPrice:'',
				  end: '',
				  ready:'',
				  over: ""
		        },
			}
		},
		watch:{
			dateForm:{
				handler(curVal,oldVal){
		console.log(curVal)
						this.$emit("setPeriod",curVal)
		},
		deep:true
			}
		},
		methods:{

		}
	}
</script>
<style >
	
</style>


